import React, { useState, useRef } from 'react';
import { Modal, Input, Form } from 'antd'

const defaultQrcode = `{"label":"yss","cid":"360************994","cidtype":"1000","name":"*","phone":"136******24","encode":"nVE/45+gYWPch1vKVuQFoyQr9SHlgBwWMhg9UehWlU9UtbEfo17tHpU1LykTsz7C","c":"G","t":1667394812,"v":3,"s":"51/edBwQvJ5Pb5e7Au9ybIj8jEbtvimv23y57cdiZk7cqmE5lq6HBTaT+/mUrRCkXc/cNjSHC6yLmazYNxf4yg=="}`
const defaultAddr = ''

const useModal = () => {

    const [visi, setVisi] = useState(false);
    const [form] = Form.useForm();
    const [qrcode, setQrcode] = useState('' || defaultQrcode);
    const [addr, setAddr] = useState('' || defaultAddr);
    const cache = useRef({
        addr: defaultAddr,
        qrcode: defaultQrcode
    });

    const open = () => {
        form.setFieldsValue(cache.current);
        setVisi(true);
    }

    const onOk = async () => {
        const formVals = await form.validateFields();
        cache.current = formVals;
        const { qrcode, addr } = formVals;
        setQrcode(qrcode);
        setAddr(addr);
        setVisi(false);
    }

    const onCancel = () => setVisi(false);

    const view = (
        <Modal visible={visi} title=""
            okText=""
            cancelText=""
            onOk={onOk}
            onCancel={onCancel}>
            <Form form={form}>
                <Form.Item label="" name="addr" rules={[{
                    required: true,
                    message: ''
                }]}>
                    <Input ></Input>
                </Form.Item>
                <Form.Item label="" name="qrcode" rules={[{
                    required: true,
                    message: ''
                }]}>
                    <Input.TextArea ></Input.TextArea>
                </Form.Item>
            </Form>
        </Modal>
    );

    return {
        open,
        view,
        qrcode,
        addr
    };

}

export default useModal;
